
  const routes = [
  {
    path: 'index',
    breadcrumbName: 'First-level Menu',
  },
  {
    path: 'first',
    breadcrumbName: 'Second-level Menu',
  },
  {
    path: 'second',
    breadcrumbName: 'Third-level Menu',
  },
];
  import { defineComponent, reactive, toRefs, watch } from 'vue';
  import {
    // MenuUnfoldOutlined,
    HomeOutlined,
    UserOutlined,
    DesktopOutlined,
    NumberOutlined,
    AppstoreOutlined,
    //  PhoneOutlined,
    //  FileTextOutlined
  } from '@ant-design/icons-vue';
  import Config from './Config.vue';
  export default defineComponent({
    setup() {
      const state = reactive({
        collapsed: false,
        selectedKeys: ['1'],
        openKeys: ['sub1'],
        preOpenKeys: ['sub1'],
        friendlyName: "wtf"
      });

      watch(
        () => state.openKeys,
        (val, oldVal) => {
          state.preOpenKeys = oldVal;
        },
      );
      const toggleCollapsed = () => {
        state.collapsed = !state.collapsed;
        state.openKeys = state.collapsed ? [] : state.preOpenKeys;
      };

      return {
        ...toRefs(state),
        toggleCollapsed,
          routes,
      };
    },
    components: {
      Config,
      HomeOutlined,
      UserOutlined,
      DesktopOutlined,
      NumberOutlined,
      AppstoreOutlined,
      //  PhoneOutlined,
      //  FileTextOutlined
    },
  });
